.form {
	// display: none;
	margin: 0 auto;
	padding: 40px 60px 30px 60px;
	max-width: 806px;
	background-color: #fff;
	&__title {
		font-size: 26px;
		font-weight: 700;
		line-height: 32px;
		color: #313131;
	}
	&__subtitle {
		padding-top: 12px;
		line-height: 19px;
		color: #313131;
	}
	&__descr {
		padding-top: 20px;
		line-height: 19px;
		opacity: 0.5;
	}
	&__input	{
		display: flex;
		flex-direction: column;
		padding-top: 6px;
		input {
			margin-bottom: 13px;
			padding: 16px;
			max-width: 452px;
			font-size: 16px;
			background-color: #efefef;
			border-radius: 5px;
			border: 1px solid transparent;
			color: rgba(0, 0, 0, 0.5);
		}
		input:last-child {
			margin-bottom: 0;
		}
		input:focus {
			border: 1px solid #c0c0c0;
		}
		input:focus::-webkit-input-placeholder {
			transition: color 0.2s;
			color: transparent
		 }
		 input:focus::-moz-placeholder {
			transition: color 0.2s;
			color: transparent
		 }
		 input:focus:-moz-placeholder {
			transition: color 0.2s;
			color: transparent
		 }
		 input:focus:-ms-input-placeholder {
			transition: color 0.2s;
			color: transparent
		 }
	}
	&__button {
		margin-top: 35px;
		width: 260px;
		height: 58px;
		font-size: 19px;
		color: #fff;
		background-color: #f78d4f;
		border-radius: 5px;
		border: 1px solid #f78d4f;
		transition: background-color 0.2s;
		img {
			margin-left: 13px;
		}
	}
	&__button:hover {
		background-color: #f86e1d;
		transition: background-color 0.2s;
	}
	&__safe {
		margin-top: 20px;
		padding: 12px 18px;
		display: flex;
		max-width: 452px;
		border: 1px solid rgba(0, 0, 0, 0.2);
		border-radius: 5px;
		img {
			align-self: flex-start;
		}
		p {
			padding-left: 13px;
			color: #313131;
			opacity: 0.7;
			line-height: 20px;
		}
	}
	&__check {
		margin-top: 40px;
		a {
			color: #f78d4f;
			line-height: 19px;
			transition: 0.2s;
		}
		a:hover {
			color: #f86e1d;
			transition: 0.2s;
		}	
	}
	&__radio {
		margin-top: 32px;
		display: flex;
		position: relative;
	}
}

// radio

.radio {	
	position: relative;
	margin-right: 25px;
	&__input	{
		appearance: none;
		position: absolute;
		top: 0;
		left: 0;
		width: 23px;
		height: 23px;
		background-color: transparent;
	}
	&__label {
		padding-left: 34px;
		line-height: 24px;
		cursor: pointer;
	}
	&__label::before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 23px;
		height: 23px;
		background-color: #efefef;
		border-radius: 30px;
		z-index: 1;
	}
	&__label::after {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 23px;
		height: 23px;
		background-color: #fff;
		border: 5px solid #ea925c;
		border-radius: 30px;
		z-index: 2;
		opacity: 0;
		transition: opacity 0.2s;
	}
}
.radio:last-child {
	margin-right: 0;
}

.radio__input:checked + .radio__label:after {
	opacity: 1;
	transition: opacity 0.2s;
}

// data

.data {
	margin-top: 15px;
	display: flex;
	align-items: center;
	position: relative;
	&__input {
		appearance: none;
		position: absolute;
		top: 0;
		left: 0;
		width: 25px;
		height: 25px;
		background-color: transparent;
	}
	&__label {
		padding-left: 36px;
		line-height: 26px;
		cursor: pointer;
	}
	&__label:before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		z-index: 1;
		width: 25px;
		height: 25px;
		background-color: #efefef;
		border-radius: 3px;
	}
	&__label::after {
		content: '';
		position: absolute;
		top: 2px;
		left: 9px;
		width: 8px;
		height: 16px;
		border: 2px solid #313131;
		border-left: none;
		border-top: none;
		z-index: 2;
		transform: rotate(45deg);
		opacity: 0;
		transition: opacity 0.2s;
	}
	&__link {
		color: #f78d4f;
		transition: 0.2s;
	}
	&__link:hover {
		color: #f86e1d;
		transition: 0.2s;
	}
}

.data__input:checked + .data__label:after {
	opacity: 1;
	transition: opacity 0.2s;
}