.authorization {
	display: none;
	max-width: 806px;
	margin: 0 auto;
	padding: 40px 60px 30px 60px;
	background-color: #fff;
	&__block-top {
		display: flex;
		align-items: center;
		flex-wrap: wrap;
		margin-top: 34px;
		input {
			padding: 15px;
			margin-right: 13px;
			max-width: 452px;
			width: 100%;
		 	height: 50px;
			font-size: 16px;
			background-color: #efefef;
			border-radius: 5px;
			border: 1px solid transparent;
			color: rgba(0, 0, 0, 0.5);
		}	
		input:focus {
			border: 1px solid #c0c0c0;
		}
		input:focus::-webkit-input-placeholder {
			transition: color 0.2s;
			color: transparent
		}
		input:focus::-moz-placeholder {
		transition: color 0.2s;
		color: transparent
		}
		input:focus:-moz-placeholder {
		transition: color 0.2s;
		color: transparent
		}
		input:focus:-ms-input-placeholder {
		transition: color 0.2s;
		color: transparent
		}
		button {
			// display: none;
			width: 178px;
			height: 50px;
			font-size: 17px;
			line-height: 21px;
			color: #fff;
			background-color: #f78d4f;
			border-radius: 5px;
			border: 1px solid #f78d4f;
			transition: background-color 0.2s;
		}
		button:hover {
			background-color: #f86e1d;
			transition: background-color 0.2s;
		}
	}
	&__block-bottom {
		p {
			padding: 15px 0 8px 0;
			color: rgba(0, 0, 0, 0.5);
		}
		input {
			text-align: center;
			margin-right: 7px;
			width: 50px;
		 	height: 50px;
			font-size: 16px;
			background-color: #efefef;
			border-radius: 5px;
			border: 1px solid transparent;
			color: rgba(0, 0, 0, 0.5);
		}	
		input:last-child {
			margin-right: 0;
		}
		input:focus {
			border: 1px solid #c0c0c0;
		}
	}
	&__link {
		margin-top: 70px;
		a {
			color: #f78d4f;
			transition: 0.2s;
		}
		a:hover {
			color: #f86e1d;
			transition: 0.2s;
		}	
	}
	&__timer {
		display: none;
		width: 178px;
		margin-top: 0;
	}
}

