.link {
	display: none;
	padding: 40px 60px 30px 60px;
	max-width: 806px;
	margin: 0 auto;
	background-color: #fff;
	&__wrapp {
		padding-right: 30px;
	}
	&__input {
		margin-top: 32px;
		display: flex;
		flex-direction: row;
		align-items: center;
		input {
			text-align: center;
			margin-right: 7px;
			width: 50px;
		 	height: 50px;
			font-size: 16px;
			background-color: #efefef;
			border-radius: 5px;
			border: 1px solid transparent;
			color: rgba(0, 0, 0, 0.5);
		}	
		input:last-child {
			margin-right: 0;
		}
		input:focus {
			border: 1px solid #c0c0c0;
		}
	}
	&__button {
		display: none;
		width: 233px;
		height: 50px;
		font-size: 17px;
		line-height: 21px;
		color: #fff;
		background-color: #f78d4f;
		border-radius: 5px;
		border: 1px solid #f78d4f;
		transition: background-color 0.2s;
	}
	&__button:hover {
		background-color: #f86e1d;
		transition: background-color 0.2s;
	}
}

.timer {
	margin-top: 30px;
	display: flex;
	&__text {
		padding-right: 4px;
	}
	&__items {
		display: flex;
	}
}