@media (max-width: 806px){
	.authorization__block-top button {
		margin-top: 20px;
	}
	.authorization__timer {
		margin-top: 20px;
	}
}
@media (max-width: 767px) {
	.form {
		padding: 30px 20px;
	}
	.wrapper {
		padding: 0 16px;
	}
	.link {
		padding: 30px 20px;
	}
	.link__input {
		flex-wrap: wrap;
	}
}

@media (max-width: 569px) {
	.link__button {
		margin-top: 20px;
	}
}

@media (max-width: 575px) {
	.header__logo {
		padding: 0 10px;
	}
	.form__title {
		font-size: 24px;
	}
	.form__radio {
		flex-direction: column;
	}
	.radio {
		margin-right: 0;
		margin-bottom: 12px;
	}
	.radio:last-child {
		margin-bottom: 0;
	}
	.form__button {
		width: 240px;
		height: 54px;
		font-size: 18px;
	}
	// .link {
	// 	padding: 30px 20px;
	// }
	// .link__wrapp {
	// 	padding-right: 0;
	// }
	.authorization {
		padding: 30px 20px;
	}
}

@media (max-width: 479px) {
	.link__wrapp {
		padding-right: 0;
  }
}
